@import "../../global";

.topbar{
  width:100%;
  height: 70px;
  background-color: white;
  color: $main-color;
  position: fixed;
  top: 0;
  z-index: 200;
  transition: all 1s ease;

  .wrapper {
    padding: 10px 30px;
    display: flex;
    align-items: center;
    justify-content: space-between;

    .left{
      display: flex;
      align-items: center;

      .logo{
        height: 50px;
        overflow-y: hidden;
        img{
          height: 100%;
        }
      }

      .icon-container{
        display: flex;
        align-items: center;
        margin: 0 30px;

        @include mobile{
          display: none;
        }

        .icon{
          font-size: 18px;
          margin-right: 5px;
        }

        span a{
          font-size: 15px;
          font-weight: 400;
          text-decoration: none;
          color: inherit;
        }
      }
    }

    .right{
      .burger-icon{
        width: 32px;
        height: 25px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        cursor: pointer;

        span{
          width: 100%;
          height: 3px;
          background-color: $main-color;
          transform-origin: left;
          transition: all 1.5s ease;
        }
      }
    }
  }

  &.active{
    background-color: $main-color;
    color: white;

    .burger-icon{
      span{
        &:first-child{
          background-color: white;
          transform: rotate(45deg);
        }
        &:nth-child(2){
          opacity: 0;
        }
        &:last-child{
          background-color: white;
          transform: rotate(-45deg);
        }
      }
    }
  }
}